.header {
  width: 100%;
  height: 80px;
  transition: all 0.3s linear;
  z-index: 10;
  background: #000;
  position: fixed;
  display: flex;
  align-items: center;
}

.header_sticky {
  transition: all 0.6s linear;
  top: 0;
  left: 0;
}

.header .nav {
  display: flex;
  align-items: center;
  height: 100%;
}

@media (max-width: 1168px) {
  .header {
    z-index: 1400;
  }
}
