.languageButton:before {
  content: "";
  float: left;
  width: 13px;
  height: 7px;
  background: url(../../assets/images/header/arrowDown.svg) no-repeat center;
  transition: all 0.3s linear;
}

.languageButton_active:before {
  transform: rotate(180deg);
  transition: all 0.3s linear;
}
